import React, { Component } from 'react'
import './ProfUI.css'
import { withStyles } from '@material-ui/core/styles';
import theme from "../utils/theme"
import axios from 'axios';
import ClearIcon from '@material-ui/icons/Clear';
import Button from "@material-ui/core/Button";
import { Typography } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import { ReactComponent as SendIcon } from '../assets/send-24px.svg';
import { ReactComponent as Avatar } from '../assets/avatar.svg';
import { ReactComponent as Voice } from '../assets/voiceIcon.svg';
import InputBase from '@material-ui/core/InputBase';

const styles = theme => ({
	textBox: {
		color: 'white',
	},
	text: {
		position: 'relative',
		borderRadius: theme.shape.borderRadius * 9,
		borderStyle: 'solid',
		borderColor: '#3CD3F7',
		backgroundColor: theme.backgroundColor,
		// marginLeft: 0,
		width: '29rem',
		height: '2rem',
		// marginBottom: '0.1rem',
		marginTop: '1rem',
		marginRight: '1rem',
		[theme.breakpoints.up('sm')]: {
			marginLeft: theme.spacing.unit,
			width: 'auto',
		},
	},
	inputRoot: {
		color: 'inherit',
		width: '100%',
	},
	inputInput: {
		paddingTop: theme.spacing.unit,
		// paddingRight: theme.spacing.unit,
		paddingBottom: theme.spacing.unit * 2,
		// paddingLeft: theme.spacing.unit * 4,
		color: 'white',
		transition: theme.transitions.create('width'),
		width: '100%',

	},
	sendIcon: {
		width: theme.spacing.unit * 3,

		height: '100%',
		position: 'relative',
		pointerEvents: 'none',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',

	},
	sendButton: {
		position: 'absolute',
		marginBottom: '2.5rem',
		marginLeft: '1.4rem',
	}
})

class ProfUI extends Component {
	constructor(props) {
		super(props);

		this.state = {
			message: '',
			messages: [],
		}
	}

	componentWillUnmount() {
		this.setState({
			message: '',
			messages: [],
		})
	}

	handleChange = event => {
		this.setState({ message: event.target.value });
	};

	handleSubmit = event => {
		event.preventDefault();
		if (!this.state.message.trim()) return;

		const msg = {
			text: this.state.message,
			user: 'human',
		};

		fetch('http://localhost:4000/chat', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				message: this.state.message,
			}),
		}).then((response) => {
			response.text().then(values => {

				const message = JSON.parse(values).message;
				const msg = {
					text: message,
					user: 'ai',
				};
				console.log('BOT:', message);
				this.setState({
					messages: [...this.state.messages, msg],
				});
			})
		})

		this.setState({
			messages: [...this.state.messages, msg],
		});

		this.setState({ message: '' });
	};

	render() {

		const ChatBubble = (text, i, className) => {
			return (
				<div key={`${className}-${i}`} className={`${className} chat-bubble`}>
					{className == "human" && <Avatar />}
					<span className="chat-content">{text}</span>
					{className == "ai" && <Voice />}
				</div>
			);
		};

		const chat = this.state.messages.map((e, index) =>
			ChatBubble(e.text, index, e.user)
		);

		const { classes } = this.props;

		return (
			<div className="Chat-container">
				<div className="Chat-row">
					<div className="Chat-column">
						<div className="chat-window">
							<div className="Chat-body">
								<div className="Chat-title">
									<div style={{ display: 'flex' }}>
										<div style={{ align: "left", width: "80%" }}>
											<Typography style={{ color: 'white', fontSize: '20px' }}>
												PROFESSOR
											</Typography>
										</div>
										<Button
											autoid="professor-close-button"
											size="small"
											onClick={this.props.close}>
											<ClearIcon />
										</Button>
									</div>
								</div>
								<div className="conversation-view">{chat}</div>
							</div>
							<div className="Chat-footer">
								<div className={classes.text}>
									<InputBase
										autoid="professor-input-box"
										style={{ width: '75%', marginLeft: "10px" }}
										id="message field"
										value={this.state.message}
										onChange={this.handleChange}
										InputProps={{
											className: classes.textBox,
										}}
										classes={{
											root: classes.inputRoot,
											input: classes.inputInput,
										}}
										label="Type here..."
									/>
									<Button
										className={classes.sendButton}
										autoid="professor-send-button"
										onClick={this.handleSubmit}>
										<SendIcon className={classes.sendIcon} />
									</Button>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
// export default withStyles(styles)(ProfUI);
export default withStyles(styles)(ProfUI);