import React, { Component } from 'react';
import Footer from "../components/Footer"
import Grid from '@material-ui/core/Grid';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { withStyles } from '@material-ui/core/styles';
import { Typography, Dialog, FormControl, Slide, Modal } from '@material-ui/core';
import { ReactComponent as ProfIcon } from "../assets/voiceIcon.svg"
import Button from '@material-ui/core/Button';
import DataTable from "../components/DataTable"
import TextField from '@material-ui/core/TextField';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import theme from '../utils/theme';
import { CSVLink, CSVDownload } from "react-csv";
import ProfUI from "../components/ProfUI"
import axios from 'axios'
import { formatter } from "../utils/formatter"
import store from "../store"
import { dateFormats } from 'highcharts';

const styles = (theme) => ({
	companyName: {
		flexGrow: 1,
		// paddingLeft: '0vw',
		// paddingRight: '0vw',
		color: '#FFFFFF',
		fontSize: '3.5vh',
		// marginTop: '',
	},
	button: {
		borderRadius: 19,
		margin: theme.spacing.unit,
		backgroundColor: 'orange'
	},
	rightIcon: {
		marginLeft: theme.spacing.unit,
	},
	root: {
		flexGrow: 1,
		paddingLeft: '1vw',
		paddingRight: '1vw',
		overflow: 'auto',
		//overflow: 'auto',
	},
	hellotext: {
		fontSize: '2vw',
		color: '#FFFFFFA6',
		height: '10vh',
		marginLeft: "10px",

	},
	card: {
		backgroundColor: theme.backgroundColor
	},
	dashBoard: {
		color: 'white',
		align: "center",
		backgroundColor: 'orange',
		height: '0.9rem',
		width: '10rem',
		position: 'absolute',
		top: '0px',
		marginBottom: '0.6rem',
		padding: '0px',
		left: '700px',
		textAlign: 'center',
		fontSize: '0.7vw',
	},
	hellotext2: {
		fontSize: '1.2vw',
		marginTop: '5vh',
		padding: '1vh',
		color: '#FFFFFF',
		backgroundColor: '#5DAAE0',
	},
	hellotext4: {
		fontSize: '1.5vw',
		marginTop: '2vh',
		// padding: '1vh',
		color: '#FFFFFF',
	},
	subheading: {
		fontSize: '0.8',
		// marginLeft: '65px',
		color: '#FFFFFFA6'
	},
	dataTableGrid: {
		overflow: 'scroll',
	},
});

class SpecificCustomer extends Component {

	constructor(props) {
		super(props);


		const customer_name = this.props.location.state.customer_name;
		const data = this.props.location.state.json_data;

		const records = []
		data.json_data.map((item, index) => {
			if (item.customer_name == customer_name)
				records.push(item);
		})

		this.state = {
			records: records,
			num_selected: 0,
			modify_disabled: true,
			export_disabled: true,
			modify_open: false,
			records_selected: [],
			modified_open_amount: '',
			modified_doc_type: '',
			csv_data: '',
			prof_active: false,
		}

		this.sumOpenAmount = this.sumOpenAmount.bind(this);
		this.update_num_selected = this.update_num_selected.bind(this);
		this.modify_clickOpen = this.modify_clickOpen.bind(this);
		this.modify_clickClose = this.modify_clickClose.bind(this);
		this.update_record_selected = this.update_record_selected.bind(this);
		this.render_dialog = this.render_dialog.bind(this);
		this.modify_clickSave = this.modify_clickSave.bind(this);
		this.handleOpenAmountChange = this.handleOpenAmountChange.bind(this);
		this.handleDocTypeChange = this.handleDocTypeChange.bind(this);
		this.onExportClick = this.onExportClick.bind(this);
		this.prof_onClick = this.prof_onClick.bind(this);
	}

	prof_onClick() {

		this.setState({
			prof_active: !this.state.prof_active,
		})
	}

	update_num_selected(num) {
		this.setState({
			num_selected: num
		})
		if (num === 1) {
			this.setState({
				modify_disabled: false,
				export_disabled: false,
			})
		}
		else if (num > 1) {
			this.setState({
				modify_disabled: true
			})
		}
		else {
			this.setState({
				modify_disabled: true,
				export_disabled: true
			})
		}
	}

	update_record_selected(records) {
		if (records) {
			if (records.length === 1) {
				this.setState({
					records_selected: records,
					modified_open_amount: records[0].actual_payment_amount,
					modified_doc_type: records[0].doctype,
				})
			}
			else {
				this.setState({
					records_selected: records,
					modified_open_amount: '',
					modified_doc_type: '',
				})
			}

		}
		else {
			this.setState({
				records_selected: [],
				modified_open_amount: '',
				modified_doc_type: '',
			})
		}

	}

	modify_clickOpen() {
		this.setState({ modify_open: true })
	}

	modify_clickClose() {
		this.setState({ modify_open: false })
	}

	handleOpenAmountChange(e) {
		this.setState({
			modified_open_amount: e.target.value
		});
	}

	handleDocTypeChange(e) {
		this.setState({
			modified_doc_type: e.target.value
		});
	}

	modify_clickSave() {
		// Update the records array as well as the store

		console.log('MOD OPEN AMOUNT', this.state.modified_open_amount);
		console.log('MOD DOC TYPE', this.state.modified_doc_type);

		const records = this.state.records;


		var newRecords = records;
		for (var i = 0; i < records.length; i++) {
			if (newRecords[i].document_number === this.state.records_selected[0].document_number &&
				newRecords[i].accout_header_id === this.state.records_selected[0].accout_header_id) {
				console.log("MODIFYING");
				newRecords[i].doctype = this.state.modified_doc_type;
				newRecords[i].actual_payment_amount = this.state.modified_open_amount;
			}
		}

		this.setState({ modify_open: false, records: newRecords });

		// Update the redux store
		// OR MAYBE NOT !


		// UPDATE THE SQL DB


		axios.post('http://localhost:8080/1705919/update_db', {
			"doctype": this.state.modified_doc_type,
			"open_amount": this.state.modified_open_amount,
			"document_number": this.state.records_selected[0].document_number,
			"account_header_id": this.state.records_selected[0].accout_header_id,
		}).then((r) => {
			console.log(r);
		}).catch((err) => {
			console.log("SQL UPDATE ERROR:", err);
		})

	}

	sumOpenAmount() {
		const records = this.state.records;
		var sum = 0;
		records.map((item, index) => {
			sum += item.actual_payment_amount;
		})
		return (sum / 1000).toFixed(2);
	}


	onExportClick() {
		console.log('RECORDS', this.state.records_selected);

		if (this.state.records_selected) {
			var objArray = this.state.records_selected;
			var array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
			var str = '';

			for (var i = 0; i < array.length; i++) {
				var line = '';
				for (var index in array[i]) {
					if (line !== '') line += ','

					line += array[i][index];
				}

				str += line + '\r\n';
			}
			console.log("CSV", str);

			this.setState({
				csv_data: str,
			})
		}
	}

	render_dialog() {

		return (
			<Dialog
				open={this.state.modify_open}
				onClose={this.handleClose}
				aria-labelledby="form-dialog-title"
				style={{ backgroundColor: theme.backgroundColor }}>

				<DialogTitle id="form-dialog-title">Modify</DialogTitle>
				<FormControl>
					<DialogContent>
						<TextField
							autoFocus
							autoid="open-amount-input"
							margin="dense"
							id="open_amount"
							label="Open Amount"
							type="number"
							defaultValue={this.state.records_selected[0].actual_payment_amount}
							onChange={this.handleOpenAmountChange}

						/>
						<TextField
							autoid="doctype-input"
							margin="dense"
							id="doctype"
							label="Document Type"
							type="text"
							defaultValue={this.state.records_selected[0].doctype}
							onChange={this.handleDocTypeChange}

						/>
					</DialogContent>
					<DialogActions>
						<Button
							autoid="modify-cancel-button"
							onClick={this.modify_clickClose}
							color="primary">
							Cancel
					</Button>
						<Button
							autoid="modify-save-button"
							onClick={this.modify_clickSave}
							color="primary">
							Save
					</Button>

					</DialogActions>
				</FormControl>
			</Dialog>
		);

	}

	render() {
		console.log('Record selected', this.state.records_selected);

		var records = this.state.records;

		const { classes } = this.props;
		const { history } = this.props;


		if (this.state.records_selected.length === 1) {
			const open_amount = this.state.records_selected.actual_payment_amount;
			const doctype = this.state.records_selected.doctype;
		}

		return (
			<div>
				<Grid container spacing={0} xs={12} style={{ overflow: 'none' }} >
					<Grid
						xs={12}
						item
						// margin='40px'
						// backgroundColor: '#252C48' 
						style={{ height: '4rem', margin: "3px", width: '100%' }}
						alignItems="center"
						direction="column">

						<div style={{ display: 'flex' }}>
							<Button
								autoid="navigation-back-button"
								onClick={() => {
									history.goBack()
								}}
							>
								<ArrowBackIcon style={{ color: 'white', margin: '10px' }} />
							</Button>
							<div style={{ width: "92%" }}>
								<Typography autoid="customer-name" className={classes.companyName}>
									{records[0].customer_name}
								</Typography>
								<Typography autoid="customer-number" className={classes.subheading}>
									{records[0].customer_number}
								</Typography>
							</div>

							<Typography className={classes.dashBoard}>Receivables Dashboard</Typography>
							<Button
								autoid="professor-button"
								variant="contained"
								color="default"
								size="small"
								className={classes.button}
								onClick={this.prof_onClick}>
								Proffesor
                  <ProfIcon className={classes.rightIcon} />
							</Button>
							<Modal open={this.state.prof_active}>
								<ProfUI close={this.prof_onClick} />

							</Modal>
						</div>


					</Grid>
					<Grid
						item
						style={{ overflow: 'auto', margin: "3px", width: '98%', height: '85vh', backgroundColor: '#252C48' }}
					>
						<Grid style={{ height: '4.5rem' }} container xs={12} direction="column">
							<div style={{ display: 'flex' }}>
								<div style={{ width: "81%" }}>

									<Button
										autoid="modify-button"
										variant="contained"
										color="primary"
										className={classes.button}
										disabled={this.state.modify_disabled}
										onClick={this.modify_clickOpen}>
										Modify
									</Button>
									{this.state.records_selected.length === 1 && this.render_dialog()}

									<Button
										autoid="export-button"
										variant="contained"
										color="primary"
										className={classes.button}
										disabled={this.state.export_disabled}
										onClick={this.onExportClick}>
										<CSVLink
											data={this.state.csv_data}
											filename={"1705919_exportedData.csv"}
										>
											Export
										</CSVLink>
									</Button>
								</div>
								<div style={{ display: 'flex' }}>
									<div>
										<Typography className={classes.hellotext4}>
											{records.length}
										</Typography>
										<p style={{ fontSize: 13, color: '#ffffff', marginRight: '1rem' }}>Total Open Invoices</p>
									</div>
									<div>
										<Typography autoid="total-open-amount-customer" className={classes.hellotext4}>
											${formatter(this.sumOpenAmount())}
										</Typography>
										<p style={{ fontSize: 13, color: '#ffffff' }}>Total Open Invoices</p>
									</div>
								</div>
								{/* <div style={{ display: 'flex' }}>
									<Typography className={classes.hellotext4}>
										Total Open Invoices
									</Typography>
									<Typography autoid="total-open-amount-customer" className={classes.hellotext4}>
										Total Open Amount
									</Typography>
								</div> */}
							</div>
						</Grid>
						<div>
							<Grid item style={{ overflow: 'none', width: "100%", height: '80%' }}>
								<DataTable
									pagination_autoid="invoice-table-pagination-customer"
									auto_id="invoice-table-customer"
									json={records}
									length={records.length}
									num_rows={10}
									updater={this.update_num_selected}
									record_update={this.update_record_selected}
									height="34.2rem" />
							</Grid>
						</div>
					</Grid>
				</Grid>

				<Footer />
			</div >
		);
	}
}

export default withStyles(styles, { withTheme: true })(SpecificCustomer);