import React, { Component } from 'react';
import Highcharts, { chart } from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import crossfilter from "crossfilter2"
import Grid from '@material-ui/core/Grid';
import theme from "../utils/theme"
import store from "../store"
import { formatter } from "../utils/formatter"

class CompanyCodeChart extends Component {

	constructor(props) {
		super(props);
		this.state = {
			json: this.props.json,
			categories: [],
			data: [],
		}
	}

	reduceAdd(p, v) {
		p.actual_payment_amount += v.actual_payment_amount;
		p.sum_days_delay += v.days_past_due_date;
		p.count += 1;
		p.avg_days_delay = p.sum_days_delay / p.count;
		return p;
	}

	reduceRemove(p, v) {
		p.actual_payment_amount -= v.actual_payment_amount;
		return p;
	}

	reduceInitial() {
		return {
			actual_payment_amount: 0,
			count: 0,
			sum_days_delay: 0,
			avg_days_delay: 0,
		};
	}

	reduceAddChart(p, v) {
		p.total_amount += v.actual_payment_amount;
		return p;
	}

	reduceRemoveChart(p, v) {
		p.total_amount -= v.actual_payment_amount;
		return p;
	}

	reduceInitialChart() {
		return {
			total_amount: 0
		};
	}

	render() {

		var jsonData = this.state.json;
		var crossfilter_data = crossfilter(jsonData);
		var businessCode = crossfilter_data.dimension(d => d.business_code);
		var total_amount = crossfilter_data.groupAll().reduce(this.reduceAdd, this.reduceRemove, this.reduceInitial)
		var total_open_invoices = crossfilter_data.dimension(d => d.isOpen);

		document.getElementById("total_customers").innerHTML = businessCode.group().size();
		document.getElementById("total_open_ar").innerHTML = '$' + formatter(total_amount.value().actual_payment_amount);
		document.getElementById("avg_days_delay").innerHTML = Math.round(total_amount.value().avg_days_delay) + " Days";
		document.getElementById("total_open_invoices").innerHTML = total_open_invoices.group().all()[1].value


		var by_business_code = crossfilter_data.dimension(d => d.business_code)
		var total_amount_by_bus_code = by_business_code.group().reduce(this.reduceAddChart, this.reduceRemoveChart, this.reduceInitialChart);

		var categories = []
		var data = []
		total_amount_by_bus_code.top(Infinity).map((item, index) => {
			categories.push(item.key);
			data.push(item.value.total_amount);
		})

		const options = {
			chart: {
				type: 'bar',
				height: 1000,
				backgroundColor: theme.backgroundColor,
			},
			title: {
				text: undefined
			},
			xAxis: {
				lineWidth: 0,
				categories: categories,
				labels: {
					style: {
						color: "#ffffff"
					}
				}
			},
			yAxis: {
				visible: false,
			},
			legend: {
				enabled: false,
			},
			tooltip: {
				enabled: false,
			},
			plotOptions: {
				series: {
					point: {
						events: {
							click: function () {
								this.select(null, true);
								var selectedPoints = this.series.chart.getSelectedPoints();


								var filteredPoints = [];
								for (let index = 0; index < selectedPoints.length; index++) {
									filteredPoints.push(selectedPoints[index].category);

								}
								function multivalue_filter(values) {
									return function (v) {
										return values.indexOf(v) !== -1;
									}
								}

								if (filteredPoints.length > 0) {
									businessCode.filterFunction(multivalue_filter(filteredPoints));
									document.getElementById("total_customers").innerHTML = filteredPoints.length;
									document.getElementById("total_open_ar").innerHTML = '$' + formatter(total_amount.value().actual_payment_amount);
									document.getElementById("avg_days_delay").innerHTML = Math.round(total_amount.value().avg_days_delay) + " Days";
									document.getElementById("total_open_invoices").innerHTML = total_open_invoices.group().all()[1].value
								} else {
									businessCode.filterAll();
									document.getElementById("total_customers").innerHTML = businessCode.group().size();
									document.getElementById("total_open_ar").innerHTML = '$' + formatter(total_amount.value().actual_payment_amount);
									document.getElementById("avg_days_delay").innerHTML = Math.round(total_amount.value().avg_days_delay) + " Days";
									document.getElementById("total_open_invoices").innerHTML = total_open_invoices.group().all()[1].value
								}
							}
						}
					}
				}
			},
			series: [{
				data: data
			}],

			credits: {
				enabled: false
			}
		};

		return (
			<Grid item style={{ overflow: 'auto', marginTop: '10px', height: '13rem', width: '26rem' }} >
				{/* // <Grid item style={{ overflow: 'auto', marginTop: '10px', height: '17%', width: '100%' }} > */}
				<HighchartsReact autoid="companycode-chart" highcharts={Highcharts} options={options} />
			</Grid >
		);
	}
}

export default CompanyCodeChart;