import React, { Component } from 'react';
import classNames from 'classnames';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TablePagination from '@material-ui/core/TablePagination';
import Checkbox from '@material-ui/core/Checkbox';

import { Toolbar, Button } from '@material-ui/core';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import FilterListIcon from '@material-ui/icons/FilterList';
import { lighten } from '@material-ui/core/styles/colorManipulator';
import DeleteIcon from '@material-ui/icons/Delete';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import theme from '../utils/theme';



class EnhancedTableHead extends React.Component {


	render() {
		const { onSelectAllClick, numSelected, rowCount } = this.props;
		return (
			<TableHead>
				<TableRow >
				<Checkbox
							indeterminate={numSelected > 0 && numSelected < rowCount}
							checked={numSelected === rowCount}
							onChange={onSelectAllClick}
							style={{ color: 'white' }}
						/>
					<TableCell style={{ color: 'white' }} padding="checkbox">Company ID
					
					</TableCell>
					<TableCell style={{ color: 'white' }}>Account Header ID</TableCell>
					<TableCell style={{ color: 'white' }}>Document Number</TableCell>
					<TableCell style={{ color: 'white' }}>Business Code</TableCell>
					<TableCell style={{ color: 'white' }}>Document Type</TableCell>
					<TableCell style={{ color: 'white' }}>Customer Number</TableCell>
					<TableCell style={{ color: 'white' }}>Customer Map ID</TableCell>
					<TableCell style={{ color: 'white' }}>Name of Customer</TableCell>
					<TableCell style={{ color: 'white' }}>Document Create Date</TableCell>
					<TableCell style={{ color: 'white' }}>Baseline Date</TableCell>
					<TableCell style={{ color: 'white' }}>Invoice ID</TableCell>
					<TableCell style={{ color: 'white' }}>Total Open</TableCell>
					<TableCell style={{ color: 'white' }}>Customer Payment Terms</TableCell>
					<TableCell style={{ color: 'white' }}>Clear Date</TableCell>
					<TableCell style={{ color: 'white' }}>Is Open Invoice</TableCell>
					<TableCell style={{ color: 'white' }}>Shipping Date</TableCell>
					<TableCell style={{ color: 'white' }}>Payment Amount</TableCell>
					<TableCell style={{ color: 'white' }}>Days past Due Date</TableCell>
					<TableCell style={{ color: 'white' }}>Actual Amount Outstanding</TableCell>
					<TableCell style={{ color: 'white' }}>Age of Invoice</TableCell>
					<TableCell style={{ color: 'white' }}>Invoice Currency</TableCell>
					<TableCell style={{ color: 'white' }}>Predicted Payment Type</TableCell>
					<TableCell style={{ color: 'white' }}>Predicted Amount</TableCell>
				</TableRow>
			</TableHead >
		);
	}
}

EnhancedTableHead.propTypes = {
	numSelected: PropTypes.number.isRequired,
	onRequestSort: PropTypes.func.isRequired,
	onSelectAllClick: PropTypes.func.isRequired,
	order: PropTypes.string.isRequired,
	orderBy: PropTypes.string.isRequired,
	rowCount: PropTypes.number.isRequired,
};

const toolbarStyles = theme => ({
	root: {
		paddingRight: theme.spacing.unit,
	},
	highlight:
		theme.palette.type === 'light'
			? {
				color: theme.palette.secondary.main,
				backgroundColor: lighten(theme.palette.secondary.light, 0.85),
			}
			: {
				color: theme.palette.text.primary,
				backgroundColor: theme.palette.secondary.dark,
			},
	spacer: {
		flex: '1 1 100%',
	},
	actions: {
		color: theme.palette.text.secondary,
	},
	title: {
		flex: '0 0 auto',
	},
});

let EnhancedTableToolbar = props => {
	const { numSelected, classes } = props;

	return (
		<Toolbar
			className={classNames(classes.root, {
				[classes.highlight]: numSelected > 0,
			})}
		>
			<div className={classes.title}>
				{numSelected > 0 ? (
					<Typography color="inherit" variant="subtitle1">
						{numSelected} selected
					</Typography>
				) : (
						<Typography variant="h6" id="tableTitle"  style={{ color: 'white' }}>
							Invoices
						</Typography>
					)}
			</div>
			<div className={classes.spacer} />
			<div className={classes.actions}>
			<form>
			<Button variant="contained" className={classes.button}>Predict</Button>
			</form>
			</div>
		</Toolbar>
	);
};

EnhancedTableToolbar.propTypes = {
	classes: PropTypes.object.isRequired,
	numSelected: PropTypes.number.isRequired,
};

EnhancedTableToolbar = withStyles(toolbarStyles)(EnhancedTableToolbar);

const styles = theme => ({
	root: {
		width: '100%',
		marginTop: theme.spacing.unit * 3,
	},
	table: {
		minWidth: 1020,
	},
	tableWrapper: {
		overflowX: 'auto',
	},
	TableCell: {
		fontColor: "white",
		color: "white",
		backgroundColor: "white"
	}
});


class DataTable extends Component {

	constructor(props) {
		super(props);
		this.state = {
			order: 'asc',
			orderBy: 'company_id',
			selected: [],
			data: this.props.json,
			length: this.props.length,
			page: 0,
			rowsPerPage: this.props.num_rows,
		}
	}


	handleSelectAllClick = event => {
		if (event.target.checked) {

			var all_selected = []
			this.props.json.map((item) => {
				all_selected.push(item);
			});

			if(this.props.updater)
				this.props.updater(all_selected.length);
					
			if(this.props.record_update)
				this.props.record_update(all_selected);

      this.setState({ selected: all_selected });
      				
			return;
		}

		this.setState({ selected: [] });
	};

	findIndex(selected, id) {
		for(var i=0; i<selected.length; i++) {
			if(selected[i].document_number == id.document_number)
				return i;
		}
		return -1;
	}

	handleClick = (event, id) => {
		const { selected } = this.state;
		// const selectedIndex = selected.indexOf(id);
		const selectedIndex = this.findIndex(selected, id);
		let newSelected = [];
		// console.log("SELECTED INDEX", selectedIndex);
		if (selectedIndex === -1) {
			newSelected = newSelected.concat(selected, id);
		} else if (selectedIndex === 0) {
			newSelected = newSelected.concat(selected.slice(1));
		} else if (selectedIndex === selected.length - 1) {
			newSelected = newSelected.concat(selected.slice(0, -1));
		} else if (selectedIndex > 0) {
			newSelected = newSelected.concat(
				selected.slice(0, selectedIndex),
				selected.slice(selectedIndex + 1),
			);
		}

		this.setState({ selected: newSelected });
		
		if(this.props.updater)
			this.props.updater(newSelected.length);
		
		if(this.props.record_update) {
			// if(newSelected.length === 1) {
				this.props.record_update(newSelected);
			// }
			// else{
			// 	this.props.record_update(null);
			// }
		}
		

	};

	handleChangePage = (event, page) => {
		this.setState({ page });
	};

	handleChangeRowsPerPage = event => {
		this.setState({ rowsPerPage: event.target.value });
	};

	isSelected = id => this.state.selected.indexOf(id) !== -1;

	componentDidMount() {  }

	render() {

		const { classes } = this.props;
		const { order, orderBy, selected, rowsPerPage, page } = this.state;
		const data = this.props.json;
		const length = this.props.length;
		const emptyRows = rowsPerPage - Math.min(rowsPerPage, length - page * rowsPerPage);

		return (
			<div>
			<Grid 
			container 
			style={{overflow: "auto", margin: "8px", height: this.props.height, width: '99.2%', backgroundColor: theme.palette.primary.main }}
			direction="column">
				
				<Table 
				autoid={this.props.auto_id}
				aria-labelledby="dataTable">
					<EnhancedTableHead
						numSelected={selected.length}
						onSelectAllClick={this.handleSelectAllClick}
						onRequestSort={this.handleRequestSort}
						rowCount={length}
						json={data}
					/>
					<TableBody>
						{data && data
							.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
							.map(n => {
								const isSelected = this.isSelected(n);
								return (
									<TableRow
										hover="trues"
										onClick={event => this.handleClick(event, n)}
										role="checkbox"
										aria-checked={isSelected}
										tabIndex={-1}
										key={n.document_number}
										selected={isSelected}
									>
										<TableCell padding="checkbox">
											<Checkbox checked={isSelected}  style={{ color: 'white' }}/>
										</TableCell>
										<TableCell component="th" scope="row" style={{ color: 'white' }}>
											{n.company_id}
										</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.acct_doc_header_id}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.document_number}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.business_code}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.doctype}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.customer_number}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.fk_customer_map_id}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.customer_name}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.document_create_date}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.baseline_create_date}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.invoice_id}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.total_open_amount}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.cust_payment_terms}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.clearing_date}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.isOpen}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.ship_date}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.paid_amount}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.days_past_due_date}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.actual_payment_amount}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.invoice_age}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.invoice_currency}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.predicted_payment_type}</TableCell>
										<TableCell align="right" style={{ color: 'white' }}>{n.predicted_amount}</TableCell>
									</TableRow>
								);
							})}
						{emptyRows > 0 && (
							<TableRow style={{ height: 49 * emptyRows }}>
								<TableCell colSpan={6} />
							</TableRow>
						)}
					</TableBody>
				</Table>
				</Grid>
				<Grid item>
				<TablePagination
					autoid={this.props.pagination_autoid}
				 	style={{ color: 'white' }}
					rowsPerPageOptions={[5, 10, 25, 50]}
					component="div"
					count={length}
					rowsPerPage={rowsPerPage}
					page={page}
					backIconButtonProps={{
						'color': 'white',
						'aria-label': 'Previous Page',
					}}
					nextIconButtonProps={{
						'color': 'white',
						'aria-label': 'Next Page',
					}}
					onChangePage={this.handleChangePage}
					onChangeRowsPerPage={this.handleChangeRowsPerPage}
				/>
				</Grid>
			</div>
		);
	}
}

export default withStyles(styles)(DataTable);