import React, { Component } from 'react';
import Footer from "../components/Footer";
import DataTable from "../components/DataTable";
import Search from "../components/Search";
import axios from 'axios';
import { SERVER_URL, ROLL_NUMBER } from "../utils/constants";
import Logo from "../assets/companyLogo.svg"
import { formatter } from "../utils/formatter"
import Button from "@material-ui/core/Button"
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import FreedaBtn from "../assets/FredaButton.png"
import { Typography, Slide, Dialog, LinearProgress, Modal } from '@material-ui/core';
import store from "../store"
import ProfUI from "../components/ProfUI"
import crossfilter from "crossfilter2"
import { ReactComponent as ProfIcon } from "../assets/voiceIcon.svg"
import CompanyCodeChart from "../components/CompanyCodeChart"


const styles = (theme) => ({
  companyName: {
    flexGrow: 1,
    paddingLeft: '1vw',
    paddingRight: '1vw',
    color: '#FFFFFF',
    fontWeight: 'bold',
    fontSize: '4vh',
    marginTop: '',
  },
  buttonProf: {
    borderRadius: 19,
    margin: theme.spacing.unit,
    backgroundColor: 'orange'
  },
  root: {
    flexGrow: 1,
    paddingLeft: '1vw',
    paddingRight: '1vw',
    overflow: 'auto',
    width: '100%',
    height: '100%',
    //overflow: 'auto',
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
  },
  hellotext: {
    fontSize: '2vw',
    color: '#FFFFFFA6',
    height: '10vh',

  },
  button: {
    alignItem: 'right',
    marginLeft: '10px',
    marginTop: '5px'
  },
  tableHead: {
    color: 'white',
    marginLeft: '15px',
    fontSize: '25px',
    width: '89%'
  },
  dashBoard: {
    color: 'white',
    align: "center",
    backgroundColor: 'orange',
    height: '0.9rem',
    width: '10rem',
    position: 'absolute',
    top: '0px',
    padding: '2px',
    left: '700px',
    textAlign: 'center',
    fontSize: '0.7vw',
  },
  hellotext4: {
    fontSize: '1.5vw',
    marginTop: '2vh',
    padding: '1vh',
    color: '#FFFFFF',
  },
  ar_text: {
    fontSize: '2.5vw',
    marginTop: '25px',
    textAlign: 'center',
    color: 'white'
  },
  searchBtn: {
    marginTop: '2vh',
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: '#5DAAE0',
    '&:hover': {
      backgroundColor: '#5daae0',
      color: 'white',
    },
  },
  dataTableGrid: {
    overflow: 'scroll',
  },
});

class CustomerDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      json: null,
      length: 0,
      prof_active: false,
      records_selected: [],
      predict_disabled: true,
      num_selected: 0,
    };
    this.prof_onClick = this.prof_onClick.bind(this);
    this.predict_onClick = this.predict_onClick.bind(this);
    this.update_record_selected = this.update_record_selected.bind(this);
    this.update_num_selected = this.update_num_selected.bind(this);

  }

  prof_onClick() {

    this.setState({
      prof_active: !this.state.prof_active,
    })
  }

  predict_onClick() {

    var data_packet = []
    this.state.records_selected.map((item, index) => {
      data_packet.push(item);
    })

    axios.post('http://localhost:5000/predict?',
      {},
      {
        headers: { 'Content-Type': 'application/json' },
        params: {
          data: {
            "id": 1705919,
            "data": data_packet
          },
        },
      }).then((r) => {

        const resp_data = r.data
        var new_json = this.state.json;

        for (let i = 0; i < resp_data.length; i++) {
          for (let j = 0; j < new_json.length; j++) {

            if (new_json[j].document_number_norm === resp_data[i].document_number_norm) {
              new_json[j].predicted_amount = resp_data[i].predictions.toFixed(2);
              if (new_json[j].predictions >= new_json[j].total_open_amount) {
                new_json[j].predicted_payment_type = "FULL PAYMENT";
              }
              else {
                new_json[j].predicted_payment_type = "PARTIAL PAYMENT";
              }

              break;
            }
          }
        }

        this.setState({ json: new_json, length: new_json.length });


      }).catch((err) => {
        console.log(err);
      })

  }

  update_record_selected(records) {

    if (records) {
      this.setState({
        records_selected: records,
      });
    }
    else {
      this.setState({
        records_selected: [],
      })
    }
  }

  update_num_selected(num) {
    this.setState({
      num_selected: num
    })
    if (num > 0) {
      this.setState({
        predict_disabled: false,
      })
    }
    else {
      this.setState({
        predict_disabled: true,
      })
    }
  }



  componentDidMount() {
    var jsonData;
    if (store.getState()[0]) {
      this.setState({ json: store.getState()[0].json_data, length: store.getState()[0].json_data.length });
      jsonData = store.getState()[0].json_data;
    }
    else {
      axios.get(
        `${SERVER_URL}${ROLL_NUMBER}/access_db`
      ).then(response => {
        const json_data = response.data;
        store.dispatch({
          type: 'SET_FROM_DASHBOARD',
          payload: {
            json_data: json_data
          }
        });
        this.setState({ json: json_data, length: json_data.length });

      }).catch(function (error) {
        throw Error.captureStackTrace(error);
      });
    }
  }

  render() {
    // console.log('Data', this.props);

    //console.log(this.state.json);
    const len = this.state.length;
    const open = Boolean(this.state.anchorEl);
    const { classes, history } = this.props;

    return (
      <div className={classes.root} style={{ overflow: 'none' }}>
        <Grid container className={classes.gridContainer} style={{ width: "100%", height: "100%" }}>
          <Grid
            item
            xs={12}
            //item style={{ margin: "3px", height: '5vh', width: '100%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column">
            {this.state.json == null && <LinearProgress />}
            <div style={{ display: "flex" }}>
              <div><img src={Logo} style={{ height: '2.8rem' }} alt="company_logo" ></img></div>
              <div style={{ width: "92%" }}><Typography className={classes.companyName}>ABC Products</Typography></div>
              <Typography className={classes.dashBoard}>Receivables Dashboard</Typography>
              <div>
                <Button
                  autoid="professor-button"
                  variant="contained"
                  color="default"
                  size="small"
                  className={classes.buttonProf}
                  onClick={this.prof_onClick}>
                  Proffesor
                  <ProfIcon className={classes.rightIcon} />
                </Button>
              </div>
            </div>
          </Grid>

          <Modal open={this.state.prof_active}>
            <ProfUI close={this.prof_onClick} />
          </Modal>


          <Grid
            container
            style={{ margin: "8px", height: '18vh', width: '24%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column"
          >
            <Typography variant="h6" style={{ width: "89%", color: 'white', textAlign: 'center' }}>
              Total Customers
            </Typography>
            <p
              id="total_customers"
              autoid="total-customers-text-collector"
              className={classes.ar_text}></p>

          </Grid>

          <Grid
            item
            style={{ margin: "8px", height: '18vh', width: '24%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column"

          >
            <Typography variant="h6" style={{ width: "89%", color: 'white', textAlign: 'center' }} >
              Total Open AR
            </Typography>
            <p
              id="total_open_ar"
              className={classes.ar_text}
              autoid="total-open-ar-text-collector"></p>

          </Grid>

          <Grid
            item
            style={{ margin: "8px", height: '18vh', width: '23%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column"
          >
            <Typography variant="h6" style={{ width: "89%", color: 'white', textAlign: 'center' }}>
              Average Days delay
            </Typography>
            <p
              id="avg_days_delay"
              className={classes.ar_text}
              autoid="average-days-delay-text-collector"></p>
          </Grid>

          <Grid
            item
            style={{ margin: "8px", height: '18vh', width: '23%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column"
            container
          >
            <Typography variant="h6" style={{ width: "89%", color: 'white', textAlign: 'center' }}>
              Total Open Invoices
            </Typography>
            <p
              autoid="total-open-invoices-customer"
              id="total_open_invoices"
              className={classes.ar_text}
            ></p>
          </Grid>

          <Grid container style={{ width: '26%', height: '63.5%' }}>
            <Grid
              container
              style={{ margin: "8px 12px 0px 5px", height: '50%', width: '96%', backgroundColor: '#252C48' }}
              alignItems="center"
              direction="column"
            >
              <div style={{ display: "flex-end" }}>
                <Typography variant="h6" style={{ color: 'white', textAlign: 'center' }}>
                  Total Amount by Company Code
                </Typography>
                {this.state.json && <CompanyCodeChart json={this.state.json} />}

              </div>
            </Grid>
            <Grid
              container
              style={{ overflow: 'none', margin: "8px 12px 0px 5px", height: '50%', width: '96%', backgroundColor: '#252C48' }}
              alignItems="center"
              direction="column"
            >
              {this.state.json && <Search json={this.state.json} />}
            </Grid>

          </Grid>

          <Grid
            item
            style={{ marginTop: "8px", overflow: 'auto', height: '64.5%', width: '73.5%', backgroundColor: '#252C48' }}
            alignItems="center"
            direction="column"
          >
            <Grid item
              style={{ height: '5vh', width: '100%', backgroundColor: '#252C48', marginTop: '3px' }}
              direction="column">
              <div style={{ display: "flex" }}>
                <Typography variant="h6" style={{ width: "89%" }} className={classes.tableHead}>
                  Invoices
                  </Typography>
                <Button
                  autoid="predict-button"
                  variant="contained"
                  style={{ height: '5vh' }}
                  disabled={this.state.predict_disabled}
                  onClick={this.predict_onClick}>
                  Predict
                  </Button>
              </div>
            </Grid>

            <Grid
              item
              style={{ overflow: 'none', width: '98.5%', height: '85%' }}
              direction="column">

              <DataTable
                pagination_autoid="invoice-table-pagination-collector"
                auto_id="invoice-table-collector"
                json={this.state.json}
                length={len}
                num_rows={10}
                updater={this.update_num_selected}
                record_update={this.update_record_selected}
                height="25.3rem" />
            </Grid>

          </Grid>

          <Footer />
        </Grid >
      </div >
    );
  }
}

export default withStyles(styles, { withTheme: true })(CustomerDetails);
